.modal-page-background {
    position: absolute;

    top: 0;
    left: 0;

    width: 100vw;
    height: 100vh;

    /* background-image: url(../../public/images/logos/no_shape_logo.png); */
}

.info-page {
    display: flex;
    flex-direction: column;

    background-color: #2e2e2e;
    color: white;

    width: 100vw;
    height: 100vh;
}

.info-page-nav-bar {
    width: 100%;
    height: 64px;

    display: flex;
    flex-direction: row;

    align-items: center;

    background-color: #000000;
    color: white;
}

.info-page-nav-bar-logo {
    height: 100%;
}

.info-page-nav-bar-title {
    font-size: 1.2em;
}

.info-page-nav-bar-title:hover {
    cursor: pointer;
    text-decoration: underline;
}

.info-page-nav-bar-middle {
    flex: 1;
}

.info-page-contents {
    flex: 1;
    margin: 20px;
}