.main-container {
    display: flex;
    flex-wrap: wrap;
    color: var(--color-text-primary);
    height: 100vh;
}

.main-part {
    flex: 1;
    display: flex;
    height: 100vh;
    box-sizing: border-box;
    padding: 5px;
    overflow: hidden;
}

.main-content {
    width: 100%;
    height: 100%;

    overflow: hidden;
    box-sizing: border-box;

    background-color: var(--color-main-content);

    display: flex;
    flex: 1;
    flex-direction: column;
}

.main-content-header {
    box-sizing: border-box;
    height: var(--header-bar-height);
    width: 100%;

    font-size: 1.5em;
    white-space: nowrap;

    padding: 10px;

    z-index: 5;

    border-bottom-style: solid;
    border-top-right-radius: 10px;
    background-color: var(--color-channel-bar);
    border-color: var(--color-accent-primary);
}

.main-content-messages-container {
    width: 100%;
    height: 100%;

    overflow: hidden;
    box-sizing: border-box;

    flex: 1;
    display: flex;
    flex-direction: column;
}

.main-content-messages {
    flex: 1;

    width: 100%;
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;

}